import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import PageUser from './panels/PageUser';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = Panel => {
		setActivePanel(Panel);
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Panel id="home">
		<PanelHeader left={<PanelHeaderBack onClick={id="pageuser"}/>}>Главная</PanelHeader>

		<Group header={<Header mode="secondary" class="Ipo">Тут будут кейсы...</Header>}></Group>

		<Tabbar>
      		<TabbarItem text="Главная">
        		<Icon28HomeOutline />
      		</TabbarItem>

			<TabbarItem indicator={<Counter size="s" mode="prominent">1</Counter>} text="Игры">
				<Icon28GameOutline />
			</TabbarItem>
			
      		<TabbarItem onClick={pageuser} text="Профиль">
				<Icon28UserCircleOutline />
			</TabbarItem>
	  		<TabbarItem text="Ещё">
				<Icon28MenuOutline />
			</TabbarItem>
    	</Tabbar>
	</Panel>
			<Persik id='persik' go={go} />

			<Panel id="pageuser">
		<PanelHeader>Профиль</PanelHeader>
		
		<Group header={<Header mode="secondary">Ваш профиль:</Header>}></Group>

		<Tabbar>
      		<TabbarItem onClick={root} text="Главная">
        		<Icon28HomeOutline />
      		</TabbarItem>

			<TabbarItem indicator={<Counter size="s" mode="prominent">NEW</Counter>} text="Игры">
				<Icon28GameOutline />
			</TabbarItem>

      		<TabbarItem text="Профиль">
				<Icon28UserCircleOutline />
			</TabbarItem>
	  		<TabbarItem text="Ещё">
				<Icon28MenuOutline />
			</TabbarItem>
    	</Tabbar>

	</Panel>
		</View>
	);
}

export default App;

